.promo {
	background: url(../img/news-bg.jpg) center/cover no-repeat;
	min-height: 100vh;
	height: 100%;
	padding-top: 110px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding-bottom: 110px;
	&__container {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}

	&__last-news {
		margin-bottom: 20px;
		color: #fff;

		font-size: 24px;
		font-weight: 400;
		line-height: 1;
	}

	&__title {
		margin-bottom: 45px;
		color: #fff;

		line-height: 1;
		text-align: center;
	}

	&__link {
		width: auto;
		align-self: center;

		padding-right: 90px;
		padding-left: 90px;

		color: #fff;

		line-height: 1;
		border-color: #fff;
		@media (min-width: $md3+px) {
			&:hover {
				color: #000;
				background-color: #fff;
			}
			&:focus {
				color: #000;
				background-color: #fff;
			}
		}
	}
}

.news {
	background-color: #151515;
	&__item {
		&:not(:last-child) {
			border-bottom: 1px solid #ffffff;
		}
	}

	&__container {
		padding-top: 50px;
		padding-bottom: 50px;
		display: flex;
		@media (max-width: $md3+px) {
			flex-wrap: wrap;
		}
	}
	&__img {
		flex: 0 0 40%;
		padding-right: 30px;
		@media (max-width: $md3+px) {
			flex: 0 0 100%;
			padding-right: 0px;
			margin-bottom: 30px;
		}
	}
	&__image {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__block {
		flex: 0 0 60%;
		display: flex;
		flex-direction: column;
		@media (max-width: $md3+px) {
			flex: 0 0 100%;
		}
	}

	&__title {
		flex: 0 0 auto;
		margin-bottom: 5px;
		color: #ffffff;
	}

	&__date {
		flex: 0 0 auto;
		display: block;
		margin-bottom: 18px;

		color: #ffffff;

		font-size: 24px;
		@media (max-width: $md3+px) {
			font-size: 18px;
		}
	}

	&__text {
		flex: 0 0 auto;
		color: #ffffff;
		&:not(:last-child) {
			margin-bottom: 15px;
		}
		@media (max-width: $md3+px) {
			text-align: justify;
		}
	}
	&__text--flex {
		flex: 1 0 auto;
	}

	&__link {
		flex: 0 0 auto;
		align-self: flex-start;
		width: auto;
		padding-right: 55px;
		padding-left: 55px;

		color: #fff;

		line-height: 1;
		border-color: #fff;
		@media (min-width: $md3+px) {
			&:hover {
				color: #000;
				background-color: #fff;
			}
			&:focus {
				color: #000;
				background-color: #fff;
			}
		}
	}
}
